<template>
  <div id="saveImage" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}">
    <headerNav class="headerNav" @headerShare='headerShare' v-if="$utils.showNavBar()"></headerNav>
    <div class="topBox" :style="{backgroundImage: `url(${commonState.imageList.list[activeIndex]})`}">
      <div class="topMask">
        <swiper class="swiper-container swiperBox" ref="swiperBox" v-if="commonState.imageList.list.length > 2" :options="swiperOptions">
          <swiper-slide class="swiper-slide iconSize slideImage" v-for="(item, index) in commonState.imageList.list" :key="index">
            <img :src="item" alt="分享图片">
          </swiper-slide>
          <!-- <div class="swiper-pagination" slot="pagination"></div> -->
        </swiper>
      </div>
      <!-- 生成海报失败模块 -->
      <div class="saveimgnotfound" v-if="(commonState.imageList.list.length < 1 && showLoading === false)">
        <div class="failbox">
          <img src="../../../assets/img/failphoto.png" alt="">
        </div>
        <p class="failtip">图片加载失败...</p>
        <div class="reloadbtnbox" @click="reloadbtn">
          <p>重新加载</p>
        </div>
      </div>
      <div class="swiper-button-next swiper-button-white" style='z-index: 12' ref="pre"></div>
      <div class="swiper-button-prev swiper-button-white" style='z-index: 12' ref="next"></div>
    </div>
    <div class="bottomTip isWechat" v-if="$utils.isWx() || $utils.isQQ()">
      <div class="count flex-row colorBase">
        <div class="swiper-pagination" v-show="commonState.imageList.list.length > 0" slot="pagination"></div>
      </div>
      <div class="title">长按上方专属海报，保存到手机后即可分享</div>
      <div class="button flex-row" @click="saveImage()">查看如何操作</div>
      <div class="tip">保存并分享专属邀请海报，成功率提升<div class="redColor">100%</div></div>
    </div>
    <div class="bottomTip" v-else>
      <div class="count colorBase flex-row">
        <div class="swiper-pagination" v-show="commonState.imageList.list.length > 0" slot="pagination"></div>
      </div>
      <div class="buttonGroup flex-row">
        <div class="button flex-row" v-if="$config.source_style >= 6"  @click="storeImage()">保存图片</div>
        <div class="button flex-row"  @click="saveImage()">{{$config.source_style >= 6 ? '立即分享' : '保存图片'}}</div>
      </div>
      <div class="tip">保存并分享专属邀请海报，成功率提升
        <div class="redColor">100%</div></div>
    </div>
    <IndexShare @shareSuccess='shareSuccess' v-if="$route.path === '/saveimage'" tag='index' :appShare='appShare'></IndexShare>
    <Guild v-if="showGuild" @closeGuild='showGuild = false'></Guild>
    <shareBox @shareAction='shareAction' v-if="showShare"></shareBox>
    <Loading v-if="showLoading"></Loading>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import IndexShare from '@/components/share/IndexShare'
import Guild from '@/components/share/save_image/showguild'
import Loading from '@/components/common/loading'
import {mapState, mapMutations} from 'vuex'
import shareBox from '@/components/common/share_box'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide,
    HeaderNav,
    IndexShare,
    Guild,
    shareBox,
    Loading
  },
  data () {
    let that = this
    return {
      imageBaseUrl: '',
      activeIndex: 0, // 当前选中第几页
      list: [],
      currentCount: 1,
      ramdomArc: {},
      appShare: {
        count: 0
      },
      showGuild: false,
      showShare: false,
      showLoading: false,
      isFirst: true,
      totalCount: 4,
      swiperOptions: {
        loop: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        },
        updateOnImagesReady: true,
        on: {
          transitionEnd () {
            that.setActive(this.realIndex)
          },
          imagesReady () {
            that.$nextTick(() => {
              if (that.commonState.imageList.list.length >= 2) {
                that.showLoading = false
              }
            })
          }
        }
      }
    }
  },
  computed: {
    ...mapState({
      commonState: state => state.common
    }),
    swiper () {
      return this.$refs.swiperBox
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_imageList',
      'common_set_vcoin'
    ]),
    setActive (index) {
      this.activeIndex = index
    },
    // 刷新当前路由重新加载
    reloadbtn () {
      this.getImageList({style: this.currentCount})
    },
    // 分享成功回调
    shareSuccess (e) {
      if (e.success === 1) {
        this.getShareReward()
      }
    },
    getRewardRules () {
      if (!this.commonState.inviteVcoin) {
        this.$api.get('MESSAGE_HINT', {}, res => {
          this.common_set_vcoin(res.data.reward_coefficient)
        })
      }
    },
    getShareReward () {
      if (this.$route.query.tag && this.$route.query.tag !== 'goods') {
        if (!this.$router.query.tag) {
          return
        }
        this.$api.get('CALLBACK_REWARD', {tag: this.$route.query.tag}, res => {
          let data = res.data
          data.type = this.commonState.shareArc[this.$route.query.tag]
          this.set_shareType(7)
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj: data
          })
          this.setShowShare(true)
        })
      }
    },
    // 顶部点击分享按钮
    headerShare () {
      this.saveImage()
    },
    // 分享回调
    shareCallback (id) {
      this.getShareReward()
    },
    // 存储图片
    storeImage () {
      let url = this.commonState.imageList.list[this.activeIndex]
      this.$utils.saveImage(url, () => {
        this.$toast({
          message: '保存成功',
          duration: 1500
        })
      })
    },
    // 分享操作
    shareAction (actions) {
      if (actions === 'cancel') {
        this.showShare = false
      } else {
        this.setAppShare(actions)
      }
    },
    // 实际分享
    setAppShare (action) {
      let url = this.commonState.imageList.list[this.activeIndex]
      let msg = {
        type: 'image',
        pictures: [url]
      }
      let flag = true
      let id = ''
      if (action === 'qq') {
        flag = true
        id = 'qq'
        // msg.pictures = [`http://sjbz.fd.zol-img.com.cn/t_s480x854c/g5/M00/0F/09/ChMkJlfJQcuIWeyfAAaVOawltHYAAU7hwPMhXYABpVR177.jpg`]
      } else if (action === 'wechat') {
        id = 'weixin'
        flag = true
        msg.extra = {
          scene: 'WXSceneSession'
        }
      } else if (action === 'friend') {
        id = 'weixin'
        msg.extra = {
          scene: 'WXSceneTimeline'
        }
      }
      if (id === 'qq') {
        this.$utils.appDownload(url, fileName => {
          msg.pictures = [fileName]
          this.shareType(id, msg, flag)
        })
      } else {
        this.shareType(id, msg, flag)
      }
    },
    // 分享提取（微信和朋友圈直接分享地址，qq分享图片）
    shareType (id, msg, flag) {
      var w = window.plus.nativeUI.showWaiting('分享中')
      setTimeout(() => {
        this.showShare = false
        w.close()
      }, 5000)
      this.commonState.shares[id].send(msg, (res) => {
        this.showShare = false
        w.close()
        if (flag) {
          this.shareCallback(id)
        } else {
          this.shareCallback(id)
        }
      }, (err) => {
        this.showShare = false
        w.close()
        if (flag) {
          if (err.code === -2) {
            this.$toast({
              message: '分享失败',
              duration: 1500
            })
          } else {
            this.$toast({
              message: '分享失败',
              duration: 1500
            })
          }
        } else {
          this.$toast({
            message: '分享失败',
            duration: 1500
          })
        }
      })
    },
    saveImage () {
      let url = this.commonState.imageList.list[this.activeIndex]
      if (this.$config.source_style >= 6) {
        this.showShare = true
      } else {
        if (this.$utils.isWx() || this.$utils.isQQ()) {
          this.showGuild = true
        } else {
          this.$utils.downImage(url)
          if (this.$route.query.tag === 'treasure_box_share') {
            // 时间戳存在并且大于昨天保存的，直接清除
            if (this.$utils.cache('saveImage') && this.$utils.getTimeStamp() > this.$utils.cache('saveImage')) {
              this.$utils.cache('saveImage', null)
            }
            // 无时间戳时进行保存回调提示奖励
            if (!this.$utils.cache('saveImage')) {
              this.shareCallback()
            } else {
              this.$utils.cache('saveImage', this.$utils.getTodayStamp())
            }
          } else {

          }
        }
      }
    },
    // 初始化
    initData () {
      this.getImageList()
    },
    getImageList (data) {
      this.$api.get('GET_RAMDOMIMAGE', data, res => {
        this.totalCount = res.data.total_count
        if (this.list.length <= res.data.total_count) {
          this.list.push(res.data.img_url)
        }
        this.currentCount++
        if (this.currentCount <= res.data.total_count) {
          this.getImageList({
            style: this.currentCount
          })
        } else {
          let obj = {
            date: this.$utils.getTimeStamp(),
            list: this.list
          }
          console.log(obj)
          this.common_set_imageList(obj)
        }
      })
    }
  },
  created () {
    if (!this.commonState.login_bol) {
      this.$toast({
        message: '请先登录'
      })
      this.$router.replace('/personal_center')
    }
    if (this.$route.query.tag === '') {

    }
    // this.getRewardRules()
  },
  beforeCreate () {
    
  },
  mounted () {
    this.showLoading = true
    setTimeout(() => {
      this.showLoading = false
    }, 5000)
    this.common_set_imageList({
      list: []
    })
    this.getImageList({style: this.currentCount})
  },
  activated () {
    this.showLoading = true
    setTimeout(() => {
      this.showLoading = false
    }, 5000)
  },
  destroyed () {
    if (this.swiper) {
      this.swiper.destroy()
    }
  }
}
</script>
<style lang="scss" scoped>
#saveImage{
  height: 100%;
  box-sizing: border-box;
  font-size: 30px;
  color: #fff;
  position: relative;
  .headerNav{
    z-index: 50000;
  }
  .topBox {
    height: 965px;
    background-repeat: repeat-x;
    background-position: center 76px;
    position: relative;
    background-size: 542px;
    .topMask {
      background: rgba($color: #000000, $alpha: .7);
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      color: #232323;
      .swiperBox {
        width: 542px;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
      }
    }
    .saveimgnotfound {
      width: 542px;
      height:965px;
      background: #F5F5F5;
      position: absolute;
      left: 14%;
      top: 0px;
      font-size: 30px;
      color: #AEACAC;
      text-align: center;
      .failbox {
        width:173px;
        height:118px;
        margin: 0 auto;
        margin-top:324px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .failtip {
        margin-top: 22px;
      }
      .reloadbtnbox {
        width: 176px;
        height: 63px;
        margin-top: 30px;
        margin-left: 183px;
        border: 1px solid #666666;
        border-radius: 31.5px;
        line-height: 63px;
        color: #000000
      }
    }
  }
  .swiper-button-white {
    &:focus{
      outline: none;
    }
  }
  .swiper-button-next {
    width: 24px;
    height: 42px;
    right: 41px;
  }
  .swiper-button-prev {
    width: 24px;
    height: 42px;
    left: 41px;
  }
  .bottomTip {
    border-top: 1px solid #f5f5f5;
    .count {
      padding: 18px 0;
      line-height: 40px;
      height: 40px;
      position: relative;
      .swiper-pagination {
        font-size: 30px;
        color: #333;
        bottom: auto;
      }
    }
    .title {
      font-size: 26px;
      color: #232323;
      // margin-top: 24px;
      margin: 4px auto 20px;
      text-align: center;
      line-height: 38px;
    }
  }
  .bottomTip.isWechat {
    .button {
      width: 310px;
      height: 60px;
    }
    .count {
      padding: 8px 0 4px;
    }
    .tip {
      padding: 15px;
    }
  }
  .button{
    background: #F66F52;
    height: 60px;
    border-radius: 37.5px;
    width: 210px;
    margin: 0 auto;
    font-weight: 900;
  }
  .buttonGroup {
    .button{
      background: #F66F52;
      height: 60px;
      border-radius: 37.5px;
      width: 210px;
      margin: 0 auto;
      font-weight: 900;
    }
    .button:first-child {
      background: #fff;
      border: 1px solid #F66F52;
      color: #F66F52;
    }
  }
  
  .tip{
    font-size: 24px;
    color: #666666;
    text-align: center;
    display: flex;
    justify-content: center;
    line-height: 33px;
    padding: 36px 0;
  }
}
.swiperBox .swiper-slide{
  background: #f2f2f2;
  width: 542px;
  height: 965px;
}
</style>



